body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-size: 1.5em;
  font-weight: 400;

  background: -webkit-linear-gradient(left, #16a085, #f4d03f);
  background: -webkit-gradient(linear, left top, right top, from(#16a085), to(#f4d03f));
  background: linear-gradient(to right, #16a085, #f4d03f);
  font-family: 'Roboto', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.AppContent {
  max-width: 450px;
  margin: 40px auto auto auto;
  box-shadow: 0 15px 20px rgba(0, 0, 0, .2);
  background-color: rgba(255, 255, 255, 0.3);

}
h1 {
  font-size: 1.8em;
  color: #fff;
  text-transform: uppercase;
  font-weight: 300;
  text-align: center;
  margin-bottom: 15px;
}

.itemContainer {
  height: 80px;
  vertical-align: center;
  border: 1px dashed #fff;
  border-width: 1px 0 0 0;
  background-color: rgba(255, 255, 255, 0.2);

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

/* Customize the label (the CheckboxContainer) */
.CheckboxContainer {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Create a custom checkbox */
.CheckboxContainer input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.Checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #fff;
}

.CheckboxContainer:hover input ~ .Checkmark {
  background-color: #ccc;
}

.CheckboxContainer input:checked ~ .Checkmark {
  background-color: #1cc7a5;
}

.Checkmark:after {
  content: '';
  position: absolute;
  display: none;
}

.CheckboxContainer input:checked ~ .Checkmark:after {
  display: block;
}

.CheckboxContainer .Checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

button {
  border: 0;
  font: inherit;
  text-transform: uppercase;
  font-weight: 300;

  display: inline-block;
  outline: 0;
  cursor: pointer;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.1);
  border-radius: 2px;

  background: #1cc7a5;
  color: #fff;
}

button:hover {
  background-color: #16a085;
}

.form {
  position: absolute;
  top: 22%;
  margin-left: -67px;
  padding: 12px 36px;
}

form {
  position: relative;
  color: #fff;
  margin: 0;
  padding: 10px 20px;
  text-transform: uppercase;
}

input {
  background: #fff;
  color: #a3a3a3;
  font: inherit;
  font-size: 0.75em;
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.1);
  border: 0;
  outline: 0;
  padding: 22px 0;
  width: 45%;
  margin-right: 5%;
}

